import React, { useContext } from 'react';
import QuestionChoiceList from '../../../components/question-types/QuestionChoiceList';
import QuestionRangeGroup from '../../../components/question-types/QuestionRangeGroup';
import QuestionSorter from '../../../components/question-types/QuestionSorter';
import { QuestionsContext } from '../../../context/QuestionsContext';
import iNeedExplorerQuestion from '../../../interface/need-explorer-question.interface';
import iQuestionOption from '../../../interface/question-option.interface';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    questions: iNeedExplorerQuestion[],

    // TEMPORARY
    activeQuestionIndex: number
}

const QuestionsPanel : React.FC<Props>  = ({
    questions,

    // TEMPORARY
    activeQuestionIndex
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('questions-panel');
    const { answerQuestion } = useContext(QuestionsContext);

    // HOOKS

    // METHODS
    const currentQuestion = () => {
        return questions[activeQuestionIndex];
    }

    const questionType = () => {
        return currentQuestion().type
    }

    const onAnswerHandler = (answer: iQuestionOption | iQuestionOption[] | number[]) => {
        answerQuestion(currentQuestion(), answer);
    }

    return (
        <div className={B()}>
            
            {
                ["CHOICE_LIST","CHOICE_BOX","CHOICE_BOX_IMAGE"].includes(questionType()) && 
                <QuestionChoiceList question={currentQuestion()} onAnswer={(answer) => onAnswerHandler(answer)}/>
            }
            
            {
                currentQuestion().type === "SLIDER_GROUP" && 
                <QuestionRangeGroup question={currentQuestion()} onAnswer={(answer) => onAnswerHandler(answer)}/>
            }

            {
                currentQuestion().type === "DRAG_AND_DROP_SORTER" && 
                <QuestionSorter question={currentQuestion()} onAnswer={(answer) => onAnswerHandler(answer)}/>
            }

        </div>
    )
}

export default QuestionsPanel;