import React, { useEffect, useState } from 'react';
import iNeedExplorerQuestion from '../../interface/need-explorer-question.interface';
import iQuestionOption from '../../interface/question-option.interface';
import useBEM from '../../utils/hooks/useBEM';


interface Props {
    question: iNeedExplorerQuestion,
    onAnswer?: (answer: iQuestionOption[]) => void
}

const QuestionSorter : React.FC<Props>  = ({
    question,
    onAnswer
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('question-sorter');
    const [toDrag, setToDrag] = useState<any>();
    const [itemToMoveIndex, setItemToMoveIndex] = useState<number>();
    const [dragStartLocation, setDragStartLocation] = useState<{
        x: number,
        y: number
    }>({
        x: 0,
        y: 0
    });

    // HOOKS
    useEffect(() => {
        const currentAnswer = (question.answer || []) as iQuestionOption[];        
        if(currentAnswer.length === 0) {
            if(onAnswer)onAnswer(question.options as iQuestionOption[]);
        }
    }, [question.answer])

    // METHODS
    const onTouchStartHandler = (e: any) => {
        const target = getCorrectTargetItem(e);

        resetPreviousDraggedItem();
        setToDrag(target);

        const {clientX, clientY} = e.targetTouches[0];
        setDragStartLocation({
            x: clientX,
            y: clientY
        });
    }

    const getCorrectTargetItem = (e: any) => {
        let target = e.target;
        const parent = target.parentNode;
        if(parent.tagName === "LI")target = parent;
        if(parent.tagName === "LABEL")target = parent.parentNode;

        return target;
    }

    const resetPreviousDraggedItem = () => {
        const previousItemToDrag = document.querySelector('.dragging') as any;
        if(previousItemToDrag){
            previousItemToDrag.style.top = `0px`;
            previousItemToDrag.classList.remove('dragging');
        }
    }

    const onTouchStartMoveHandler = (e: any, index: number) => {
        const {clientX, clientY} = e.targetTouches[0];

        setItemToMoveIndex(index);
        const newLocation = clientY - dragStartLocation.y;
        toDrag.style.top = `${newLocation}px`;
        toDrag.classList.add('dragging');
    }

    const isTruthy = (value: any) => {
        return value !== null && value !== undefined;
    }

    const onTouchEndHandler = () => {
        const index = getItemIndexToSwap();
        if(isTruthy(index) && isTruthy(itemToMoveIndex)){
            swapItems(itemToMoveIndex as number, index as number);
        }

        setToDrag(null);
        resetPreviousDraggedItem();        
    }

    const swapItems = (indexA: number, indexB: number) => {
        if(indexA === indexB)return;

        const newAnswers = question.answer as iQuestionOption[];
        const tempA = newAnswers[indexA];
        newAnswers[indexA] = newAnswers[indexB];
        newAnswers[indexB] = tempA;

        if(onAnswer)onAnswer(newAnswers);
    }

    const getItemIndexToSwap = () => {
        const options = document.querySelectorAll('.question-sorter__option');
        const draggedItemHeight = toDrag.offsetTop+toDrag.offsetHeight;
        let indexToSwap: number | undefined;

        options.forEach((item: any, index: number) => {
            if(!item.classList.contains('dragging')){
                const targetItemHeight = item.offsetTop+item.offsetHeight;

                let pasok = toDrag.offsetTop >= item.offsetTop && toDrag.offsetTop <= targetItemHeight;
                pasok = !pasok ? false : (toDrag.offsetTop - item.offsetTop) <= 35;

                if(!pasok){
                    pasok = draggedItemHeight <= targetItemHeight && draggedItemHeight >= item.offsetTop;
                    pasok = !pasok ? false : targetItemHeight - draggedItemHeight <= 35;    
                }

                if(pasok)indexToSwap = index;    
            }
        });
        return indexToSwap;
    }

    return (
        <div className={B()}>
            <h2 className={E('question')}>{question.question}</h2>

            <div className={E('items-container')}>
                <ul className={E('options')}>
                    {(question.answer as iQuestionOption[]).map((option, index) => (
                        <li 
                            onTouchStart={onTouchStartHandler}
                            onTouchMove={(e) => onTouchStartMoveHandler(e, index)}
                            onTouchEnd={onTouchEndHandler}

                            className={E('option')} 
                            key={option.value}>
                            
                            <div className={E('option-icon')} ></div>

                            <label>
                                {option.label}

                                <span>{index + 1}</span>
                            </label>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    )
}

export default QuestionSorter;