import React from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import QuestionsProvider from './context/QuestionsContext';
import ClosingPage from './pages/closing-page/ClosingPage';
import HomePage from './pages/home-page/HomePage';
import QuestionnairesPage from './pages/questionnaires-page/QuestionnairesPage';

function App() {
  return (
    <div className="app">

      <QuestionsProvider>
        <Router>
          <Switch> 
            <Route path="/" exact component={HomePage}/>
            <Route path="/questions" exact component={QuestionnairesPage}/>
            <Route path="/closing" exact component={ClosingPage}/>          
          </Switch>      
        </Router>
      </QuestionsProvider>

    </div>
  );
}

export default App;
