import React, { createContext, useState, FC } from "react";
import GeneralQuestions from "../constants/questions.constant";
import iNeedExplorerQuestion from "../interface/need-explorer-question.interface";
import iQuestionOption from "../interface/question-option.interface";

export type QuestionsContextState = {
    questions: iNeedExplorerQuestion[];
    answerQuestion: (question: iNeedExplorerQuestion, answer: iQuestionOption | iQuestionOption[] | number[]) => void;
};

const contextDefaultValues: QuestionsContextState = {
    questions: GeneralQuestions.questions,
    answerQuestion: (question: iNeedExplorerQuestion, answer: iQuestionOption | iQuestionOption[] | number[]) => {}
};

export const QuestionsContext = createContext<QuestionsContextState>(
    contextDefaultValues
);

const QuestionsProvider: FC = ({ children }) => {
  const [questions, setQuestions] = useState<iNeedExplorerQuestion[]>(contextDefaultValues.questions);

  const answerQuestion = (question: iNeedExplorerQuestion, answer: iQuestionOption | iQuestionOption[] | number[]) => {
    let newState: iNeedExplorerQuestion[] = [];

    if(["CHOICE_LIST","CHOICE_BOX","CHOICE_BOX_IMAGE"].includes(question.type)){
      newState = updateChoiceListAnswer(question, answer as iQuestionOption);  

    }else if(question.type === "DRAG_AND_DROP_SORTER"){
      newState = updateQuestionAnswer(question, answer as iQuestionOption[]);   

    }else if(question.type === "SLIDER_GROUP"){
      newState = updateQuestionAnswer(question, answer as number[]); 
           
    }

    setQuestions(newState)
  };

  const updateQuestionAnswer = (question: iNeedExplorerQuestion, answer: iQuestionOption[] | number[]) => {
    const newState = questions.map( x => {
      if(x.id === question.id)return { ...x, answer }
      return x;
    });
    return newState;
  }

  const updateChoiceListAnswer = (question: iNeedExplorerQuestion, answer: iQuestionOption) => {
    const newState = questions.map( x => {
      // NOte: update the target question
      if(x.id === question.id){      
          // Note: for multiple answers
          if(question.canSelectMultiple) {
              const currentAnswer = (x.answer ? x.answer : []) as iQuestionOption[];
              const removedAnswerIfAlreadyExist = currentAnswer.filter( x => x.value !== answer.value);
              const isAnswerAlreadyExist = currentAnswer.length !== removedAnswerIfAlreadyExist.length;

              let finalAnswer = isAnswerAlreadyExist ? 
                  [...removedAnswerIfAlreadyExist]: 
                  [ ...currentAnswer, answer ];

              // NOte: Temporary fix for scpeacial scenario
              if(question.id === '1.3') {
                  finalAnswer = answer.value === '1.3.3' ? 
                  [answer] : // one answer only
                  finalAnswer.filter(x => x.value !== '1.3.3') // remove the 1.3.3
              }

              return {
                  ...x,
                  answer: finalAnswer
              }
          }
          return { ...x, answer: answer }
      }

      return x;
    });
    return newState;
  }

  return (
    <QuestionsContext.Provider
      value={{
        questions,
        answerQuestion
      }}
    >
      {children}
    </QuestionsContext.Provider>
  );
};

export default QuestionsProvider;