import React, { useContext, useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import Arrow from '../../components/ui/Arrow';
import Button from '../../components/ui/Button';
import { QuestionsContext } from '../../context/QuestionsContext';
import iQuestionOption from '../../interface/question-option.interface';
import useBEM from '../../utils/hooks/useBEM';
import QuestionsNav from './components/QuestionsNav';
import QuestionsPanel from './components/QuestionsPanel';

interface Props {

}

const QuestionnairesPage : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('questionnaires-page');
    const history = useHistory();
    const { questions } = useContext(QuestionsContext)

    // TEMPORARY
    const [activeQuestionIndex, setActiveQuestionIndex] = useState(0);

    // HOOKS

    // METHODS
    const previousQuestion = () => {
        if(activeQuestionIndex === 0){
            history.push('/');
            return;
        }
        setActiveQuestionIndex(prev => prev - 1)
    }

    const nextQuestion = () => {
        if(questions.length === (activeQuestionIndex+1)){
            history.push('/closing');
            return;
        }
        setActiveQuestionIndex(prev => prev + 1)
    }

    const currentQuestion = () => {
        return questions[activeQuestionIndex];
    }

    const isQuestionNotAnswered = () => {
        const currentAnswer = currentQuestion().answer;

        if(currentQuestion().canSelectMultiple) {
            const answers = (currentAnswer || []) as iQuestionOption[];
            return answers.length === 0;
        }

        return !currentAnswer;
    }

    return (
        <div className={B()}>
            <QuestionsNav numberOfPage={questions.length} activePage={activeQuestionIndex}/>

            {/* TEMPORARY activeQuestionIndex */}
            <QuestionsPanel 
                activeQuestionIndex={activeQuestionIndex}
                questions={questions}/>

            <div className={E('buttons', activeQuestionIndex < 1 ? 'first-question' : '')}>
                {
                    activeQuestionIndex > 0 &&
                    <Button click={previousQuestion} type="outlined">
                        <Arrow circle background="#f1f1f1" type="left"/>
                    </Button>
                }
                <Button 
                    click={nextQuestion} 
                    disabled={isQuestionNotAnswered()}>NEXT</Button>
            </div>
        </div>
    )
}

export default QuestionnairesPage;