import React, { useState } from 'react';
import iNeedExplorerQuestion from '../../interface/need-explorer-question.interface';
import iQuestionSlider from '../../interface/question-slider.interface';
import useBEM from '../../utils/hooks/useBEM';

interface Props {
    question: iNeedExplorerQuestion,
    onAnswer?: (answer: number[]) => void
}

const QuestionRange : React.FC<Props>  = ({
    question,
    onAnswer
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('question-range-group');
    const [localAnswers, setLocalAnswers] = useState<number[]>(question.answer as number[]);

    // HOOKS

    // METHODS
    const isWithValue = (value: any) => {
        return value !== null && value !== undefined;
    }

    const updateAnswer = (todo: '+' | '-', index: number) => {

            const answers = question.answer as number[];
            const currentValue = answers[index];
            let newValue = todo === "+" ? (currentValue + 1) : (currentValue - 1);
            const sliders = question.sliders as iQuestionSlider[];
            const min = sliders[index].min as number;
            const max = sliders[index].max as number;
            
            // Note: validate min max value
            if(isWithValue(min) && newValue < min || isWithValue(max) && newValue > max){
                newValue = currentValue;
            }

            const newAnswers = answers.map( (x, i) => {
                if(i === index)return newValue;
                return x;
            });

            if(onAnswer)onAnswer(newAnswers);
    }

    return (
        <div className={B()}>
            <h2 className={E('question')}>{question.question}</h2>

            <div className={E('items-container')}>
                <ul className={E('items')}>
                    {question.sliders?.map((item, index: any) => (
                        <li 
                            className={E('item')} 
                            key={item.key}>
                            
                            <label>{item.label}</label>
                            
                            <div className={E('control')} >
                                <div className={E('control-button')} onClick={() => updateAnswer('-', index)}>-</div>

                                <h2>
                                    <span>{(question.answer as number[])[index]}</span>
                                    Hours
                                </h2>

                                <div className={E('control-button')} onClick={() => updateAnswer('+', index)}></div>
                            </div>

                        </li>
                    ))}
                </ul>
            </div>
        </div>
    )
}

export default QuestionRange;